<template>
  <el-form 
    :model="form" 
    :rules="rules" 
    :label-position="labelPosition" 
    :disabled="readOnly"
    ref="RSVAutoForm"
    label-width="200px" 
    size="mini">
    <el-form-item label="Gi tr nh gi" prop="valuation_value">
      <el-input 
        v-model.number="form.valuation_value"
        clearable>
        <template slot="append">ng</template>
      </el-input>
    </el-form-item>
    
    <el-form-item label="NV ang m bo" prop="guarantee_value">
      <el-input 
        v-model.number="form.guarantee_value"
        clearable>
        <template slot="append">ng</template>
      </el-input>
    </el-form-item>

    <el-form-item label="Thng tin xe" prop="car_detail">
      <el-cascader v-if="!readOnly" v-model="form.car_detail" :options="cascader" :props="props" clearable />
      <el-input v-else :value="form.car_detail.join(' / ')" :title="form.car_detail.join(' / ')" />
    </el-form-item>

    <el-form-item prop="no_years_different">
      <label slot="label">
        S nm chnh lch
        <el-tooltip effect="dark" placement="top">
          <div slot="content">
             chnh gia nm bt u s dng v nm sn xut
          </div>
          <i class="el-icon-warning" />
        </el-tooltip>
      </label>
      <el-radio-group v-model="form.no_years_different">
        <el-radio-button label="0" value="0" />
        <el-radio-button label="1" value="1" />
        <el-radio-button label="2" value="2" />
      </el-radio-group>
    </el-form-item>

    <el-form-item label="S ch" prop="no_seats">
      <el-input-number v-model.number="form.no_seats" controls-position="right" :min="0" />
    </el-form-item>

    <el-form-item prop="load">
      <label slot="label">
        Ti trng
        <el-tooltip effect="dark" placement="top">
          <div slot="content">
            n v tn
          </div>
          <i class="el-icon-warning" />
        </el-tooltip>
      </label>
      <el-input-number v-model.number="form.load" controls-position="right" :min="0" />
    </el-form-item>

    <el-form-item label="Gi tr HMB xe" prop="price">
      <el-input v-model.number="form.price">
        <template slot="append">ng</template>
      </el-input>
    </el-form-item>

    <el-form-item label="Nm sn xut" prop="manufacturing_year">
      <el-input-number v-model.number="form.manufacturing_year" controls-position="right" :min="1900" :max="new Date().getFullYear()" />
    </el-form-item>

    <el-form-item label="S Km  i" prop="traveled_distance">
      <el-input-number v-model.number="form.traveled_distance" controls-position="right" :min="0" />
    </el-form-item>

    <el-form-item label="S khung" prop="VIN">
      <el-input v-model="form.VIN" />
    </el-form-item>

    <el-form-item label="S my" prop="engine_number">
      <el-input v-model="form.engine_number" />
    </el-form-item>

    <el-form-item label="Bin kim sot" prop="plate_number">
      <el-input v-model="form.plate_number" />
    </el-form-item>
  </el-form>
</template>

<script>
import { cascader } from '@/api/data_sample'
export default {
  name: 'RSV-auto',

  props: {
    readOnly: {
      type: Boolean,
      default: () => false
    },

    formData: {
      type: Object,
      default: () => {}
    }
  },

  data() {
    let car_detail = []
    const self = this
    return {
      labelPosition: 'left',
      formName: 'RSVAutoForm',
      // Dataset
      cascader: cascader,
      form: {
        type: 'auto',
        description: 't',
        valuation_value: null,
        guarantee_value: null,
        car_detail: [],
        no_seats: null,
        no_years_different: null,
        load: null,
        price: null,
        manufacturing_year: null,
        traveled_distance: null,
        VIN: null,
        engine_number: null,
        plate_number: null
      },
      title: {
        2: 'Ni sn xut',
        3: 'Hng xe',
        4: 'Dng xe',
        5: 'Dng xe chi tit',
        6: 'Kiu xe',
        7: 'Dung tch',
        8: 'Hp s'
      },
      props: {
        lazy: true,
        async lazyLoad(node, resolve) {
          const { level } = node

          car_detail = car_detail.slice(0, level)
          car_detail[level - 1] = node.value

          if (!['Xe mi', 'Xe c'].includes(node.value)) {
            setTimeout(() => {
              self.$store.dispatch(
                'iDecision/retrieveReferenceData', 
                { dataset: 'auto', car_detail: car_detail.slice(0, level).join(',') },
                { root: true }
              ).then(res => {
                const node = res.map(item => ({
                  value: item,
                  label: item,
                  leaf: level >= 8
                }))
                node.unshift({
                  value: self.title[level],
                  label: self.title[level],
                  disabled: true
                })
                resolve([...new Set(node)])
              })
            }, 300)
          } else {
            resolve([
              { value: 'Loi xe', label: 'Loi xe', disabled: true },
              { value: 'Xe ti', label: 'Xe ti', leaf: false },
              { value: 'Xe du lch', label: 'Xe du lch', leaf: false },
              { value: 'Xe khch', label: 'Xe khch', leaf: false }
            ])
          }
        }
      },
      rules: {
        valuation_value    : [{ required: true, message: 'Vui lng chn ', trigger: 'blur' }],
        guarantee_value    : [{ required: true, message: 'Vui lng chn ', trigger: 'blur' }],
        car_detail         : [{ required: true, message: 'Vui lng chn ', trigger: 'change' }],
        no_seats           : [{ required: true, message: 'Vui lng nhp ', trigger: 'blur' }],
        no_years_different : [{ required: true, message: 'Vui lng nhp ', trigger: 'change' }],
        price              : [{ required: true, message: 'Vui lng nhp ', trigger: 'blur' }],
        manufacturing_year : [{ required: true, message: 'Vui lng nhp ', trigger: 'blur' }],
        traveled_distance  : [{ required: true, message: 'Vui lng nhp ', trigger: 'blur' }],
        VIN                : [{ required: true, message: 'Vui lng nhp ', trigger: 'blur' }]
      }
    }
  },

  mounted() {
    if (this.formData) this.form = this.formData
  },

  methods: {
    submit(keep) {
      this.$refs[this.formName].validate((valid) => {
        if (valid) {
          this.$store.dispatch(
            'iDecision/addCollateral', 
            { payload: this.form, path: 'auto', keep: keep }, 
            { root: true } 
          )
        } else {
          return false
        }
      })
    }
  }
}
</script>



